import Utils from '../../utils';
import { Node, SettingType} from '../../node';
import { Type } from '../../node-io';

export default class JsonMultiple {

    static renameInputsOutputs(that) {
        that.filterKeys = []
        for (let i = 1; i <= that.outputsCount; i++) {
            that.filterKeys.push(that.settings['output' + i].value)
            const output = that.settings['output' + i].value || '';
            that.outputs[i].name = '' + i + ' | ' + output;
            that.setOutputData(i, '', output);
        }
    }

    static changeOutputsAndSettings(that) {
        let outputs = that.settings['outputs_count'].value;
        outputs = Utils.clamp(outputs, 1, 10);
        that.settings['outputs_count'].value = outputs;
        // change outputs count
        if (that.outputsCount != outputs) JsonMultiple.changeMyOutputsCount(outputs, that);
      }

      static changeMyOutputsCount(target_count, that) {
        const diff = target_count - that.outputsCount;
        if (diff == 0) return;
        // add inputs, outputs
        that.changeOutputsCount(target_count + 1, Type.STRING);
    
        // change settings for outputs
        if (diff > 0) {
          for (let i = that.outputsCount + 1; i <= target_count; i++) {
            that.settings['output' + i] = {
              description: 'Output ' + i,
              value: '',
              type: SettingType.STRING,
            };
            for (let i = that.outputsCount + 1; i <= target_count; i++) {
                that.settingConfigs.groups.push({
                [`output${i}`]: { weight: 5 },
              });
            }
          }
        } else if (diff < 0) {
          for (let i = that.outputsCount; i > target_count; i--) {
            delete that.settings['output' + i];
            that.settingConfigs.groups.pop();
          }
        }
    
        that.outputsCount = target_count;
      }
    
}
